::-webkit-input-placeholder {
  color: #d3d3d2;
}

::-moz-placeholder {
 color: #d3d3d2;
}

:-moz-placeholder { 
 color: #d3d3d2;
}

:-ms-input-placeholder {
 color: #d3d3d2;
}

.button {
  border: none;

  color: #ffffff;
  text-decoration: none;

  background: linear-gradient(to bottom, #f26843, #e84c36);
  box-shadow: 0px 2px 5px 0px #b0350f;

  cursor: pointer;
  outline: none;
}

.button:hover,
.button:focus {
  background: #f17c64;
}

.button:active {
  background: linear-gradient(to top, #f26843, #e84c36);
  box-shadow: inset 0px 2px 5px 0px #b0350f;
}